export default class AppModel {
  constructor(request) {
    this.request = request;
    this.videoInfo = {
      nextPageToken: null,
      id: [],
      titles: [],
      thumbnails: [],
      descriptions: [],
      channelTitles: [],
      uploadDates: [],
      viewCounts: [],
    };
  }

  async takeData() {
    const url = this.request;
    const responce = await fetch(url);
    const data = await responce.json();

    data.items.forEach(item => this.videoInfo.id.push(item.id.videoId));
    this.videoInfo.nextPageToken = data.nextPageToken;
  }

  async extractVideoInfo() {
    await this.takeData();
    const url = `https://www.googleapis.com/youtube/v3/videos?key=AIzaSyCTWC75i70moJLzyNh3tt4jzCljZcRkU8Y&id=${this.videoInfo.id.join(',')}&part=snippet,statistics`;
    const responce = await fetch(url);
    const data = await responce.json();
    data.items.forEach(e => this.videoInfo.titles.push(e.snippet.title));
    data.items.forEach(e => this.videoInfo.thumbnails.push(e.snippet.thumbnails.medium.url));
    data.items.forEach(e => this.videoInfo.descriptions.push(e.snippet.description));
    data.items.forEach(e => this.videoInfo.channelTitles.push(e.snippet.channelTitle));
    data.items.forEach(e => this.videoInfo.uploadDates.push(e.snippet.publishedAt));
    data.items.forEach(e => this.videoInfo.viewCounts.push(e.statistics.viewCount));
    return this.videoInfo;
  }
}
